select{
  min-width: 160px;
  min-height: 45px;
  padding: 10px 15px;
  padding-right: 50px;
  font-size: 1.1rem;
  background-color: rgb(50, 50, 50);
  color: white;
  border-radius: 10px;
  appearance:none;
  outline: none;
  border: none;

  background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='30' viewBox='0 0 24 24' width='30' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
  background-repeat: no-repeat;
  background-position-x: calc(100% - 10px);
  background-position-y: 50%;
}
select:focus{
  border-radius: 10px 10px 0 0;
}

header{
  height: 80px;
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 210px;
}
#video-container{
  height: 200vh;
}

#video-container video{
  z-index: -1000;
  width:100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  top: 0;
}

.main-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgb(25,150,150);
  background: linear-gradient(180deg, rgba(20,130,130,0) 0%, rgba(20,130,130,.9) 100%);
  padding: 60px 0;
}

.main-section h1{
  margin-top: -25px;
  font-size: 5.65rem;
  font-weight: 900;
  color: rgb(212, 163, 72);
  text-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
}

.main-section h2{
  margin-top: -40px;
  font-size: 2.7rem;
  font-weight: 700;
  color: rgb(212, 212, 212);
  text-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
  display:inline-block;
  transform:scale(1,1.2);
}
.big-button{
  margin-top: -220px;
}

.button-text{
  text-align: center;
  font-size: 2.8rem;
  font-weight: 900;
  display:inline-block;
  transform:scale(1,1.2) translateY(-250px);
  line-height: 1;
  color: rgb(12, 209, 209);
  text-shadow: 0 3px 3px rgba(0, 0, 0, 0.5);
}
.button-text > p > small{
  font-size: 2rem;
  font-weight: 400;
}

.welcome-logo{
  margin-top: -200px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-logo p{
  display: inline;
  font-size: 2.8rem;
  font-style: italic;
  font-weight: 700;
  margin-right: 10px;
  color: rgb(212, 163, 72);
  text-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
}
.welcome-logo ul{
  font-size: 1.5rem;
  font-weight: 400;
  color: rgb(230, 189, 115);
  text-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
  line-height: 1.6;
  display:inline-block;
  transform:scale(1,1.2);
}
.welcome-logo ul li span{
  font-weight: 600;
}

.tables-section{
  background: rgba(3, 68, 68, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;
  position: relative;
}

.tables-section::before {    
  content: "";

  background-image: url("./images/dragon.webp");
  background-size:135%;
  background-position: center -450px;
  background-repeat: no-repeat;
  background-attachment: fixed;

  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: .65;
}

.tables-section *{
  z-index: 1;
}

.tables-section h1{
  font-size: 1.75rem;
  color: rgb(13, 201, 201);
  display:inline-block;
  transform:scale(1,1.1);
  margin-top: -50px;
  margin-bottom: 40px;
}

.tables-table{
  margin-bottom: 80px;
}

footer{
  background-color: black;
  color: rgb(182, 181, 181);
  padding: 20px 200px;
}
footer h1{
  font-size: 1rem;
}

footer ul li{
  font-weight: 500;
  margin-left: -10px;
}

footer a{
  color: rgb(182, 181, 181);
}

footer *{
  font-size: .9rem;
  font-weight: 400;
  line-height: 1.7;
}

footer p a{
  color: rgb(211, 41, 41);
}
